#wms_search .container {
  margin: 0 auto;
  max-width: 112.0rem;
  padding: 0 2.0rem;
  position: relative;
  width: 100%;
}

#wms_search .row {
  display: flex;
  flex-direction: column;
  padding: 0;
  width: 100%;
}

#wms_search .row.row-no-padding {
  padding: 0;
}

#wms_search .row.row-no-padding > .column {
  padding: 0;
}

#wms_search .row.row-wrap {
  flex-wrap: wrap;
}

#wms_search .row.row-top {
  align-items: flex-start;
}

#wms_search .row.row-bottom {
  align-items: flex-end;
}

#wms_search .row.row-center {
  align-items: center;
}

#wms_search .row.row-stretch {
  align-items: stretch;
}

#wms_search .row.row-baseline {
  align-items: baseline;
}

#wms_search .row .column {
  display: block;
  flex: 1 1 auto;
  margin-left: 0;
  max-width: 100%;
  width: 100%;
}

#wms_search .row .column.column-offset-10 {
  margin-left: 10%;
}

#wms_search .row .column.column-offset-20 {
  margin-left: 20%;
}

#wms_search .row .column.column-offset-25 {
  margin-left: 25%;
}

#wms_search .row .column.column-offset-33, #wms_search .row .column.column-offset-34 {
  margin-left: 33.3333%;
}

#wms_search .row .column.column-offset-50 {
  margin-left: 50%;
}

#wms_search .row .column.column-offset-66, #wms_search .row .column.column-offset-67 {
  margin-left: 66.6666%;
}

#wms_search .row .column.column-offset-75 {
  margin-left: 75%;
}

#wms_search .row .column.column-offset-80 {
  margin-left: 80%;
}

#wms_search .row .column.column-offset-90 {
  margin-left: 90%;
}

#wms_search .row .column.column-10 {
  flex: 0 0 10%;
  max-width: 10%;
}

#wms_search .row .column.column-20 {
  flex: 0 0 20%;
  max-width: 20%;
}

#wms_search .row .column.column-25 {
  flex: 0 0 25%;
  max-width: 25%;
}

#wms_search .row .column.column-33, #wms_search .row .column.column-34 {
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
}

#wms_search .row .column.column-40 {
  flex: 0 0 40%;
  max-width: 40%;
}

#wms_search .row .column.column-50 {
  flex: 0 0 50%;
  max-width: 50%;
}

#wms_search .row .column.column-60 {
  flex: 0 0 60%;
  max-width: 60%;
}

#wms_search .row .column.column-66, #wms_search .row .column.column-67 {
  flex: 0 0 66.6666%;
  max-width: 66.6666%;
}

#wms_search .row .column.column-75 {
  flex: 0 0 75%;
  max-width: 75%;
}

#wms_search .row .column.column-80 {
  flex: 0 0 80%;
  max-width: 80%;
}

#wms_search .row .column.column-90 {
  flex: 0 0 90%;
  max-width: 90%;
}

#wms_search .row .column .column-top {
  align-self: flex-start;
}

#wms_search .row .column .column-bottom {
  align-self: flex-end;
}

#wms_search .row .column .column-center {
  align-self: center;
}

@media (min-width: 40rem) {
  #wms_search .row {
    flex-direction: row;
    margin-left: -1.0rem;
    width: calc(100% + 2.0rem);
  }
  #wms_search .row .column {
    margin-bottom: inherit;
    padding: 0 1.0rem;
  }
}
